<template>
  <div
    class="the-assets-crypto__body-item e-asset-crypto-item"
    :class="{
      'is-active': activeAsset.replace(/USDT|USD/g, '') === asset.symbol,
      'e-asset-crypto-item__increase': change === 'up',
      'e-asset-crypto-item__decrease': change === 'down',
    }"
    @click="handleSelect(asset.recommendedContract)"
  >
    <div class="the-assets-crypto__item-col e-asset-crypto-item__asset-symbol">
      {{asset.symbol}}
    </div>
    <div class="the-assets-crypto__item-col the-assets-crypto__item-price">
      <e-big-number :value="asset.lastPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimalPlaces="asset.decimals" v-if="asset.lastPrice && !asset.lastPrice.isZero()" />
    </div>
    <!--<div class="the-assets-crypto__item-col" v-if="APP_ENV === 'test'">-->
    <div class="the-assets-crypto__item-col">
      <e-big-number :value="asset.volume24h" :decimals="FUTURES_AMOUNT_DECIMALS" :decimalPlaces="0" v-if="asset.volume24h" />
    </div>
    <div class="the-assets-crypto__item-col">
      <span
        class="the-assets-crypto__change"
        :class="asset.isTradePriceIncreasing ? 'the-assets-crypto__change-increase' : 'the-assets-crypto__change-decrease'"
      >
        <e-big-number
          :value="asset.tradePriceDiffPct"
          :decimal-places="2"
          sign
        />%
      </span>
    </div>
    <div class="the-assets-crypto__item-col">
      <a :href="`https://docs.dmex.app/assets/${asset.symbol.toLowerCase()}`" @click.stop target="_blank" class="e-asset-crypto-item__link"><e-icon :src="$options.icons.iconLink" class="e-asset-crypto-item__asset-link"/></a>
    </div>
    <!--<div class="the-assets-crypto__item-col">-->
    <!--{{asset.maxMargin || '?'}}x-->
    <!--</div>-->
  </div>
</template>

<script>
import EBigNumber from '@/components/EBigNumber/';
import EIcon from '@/components/EIcon/';

import iconLink from '@/icons/e-icon__link2.svg';

export default {
  name: 'e-asset-crypto-item',

  data() {
    return {
      //    
      timerId: null,

      change: null,
    };
  },

  icons: {
    iconLink,
  },

  props: {
    asset: {},
    activeAsset: {},
  },

  watch: {
    asset(nextAsset, prevAsset) {
      if (nextAsset.lastPrice && nextAsset.lastPrice.gt(prevAsset.lastPrice)) {
        this.change = 'up';
        this.timerId = setTimeout(() => {
          this.change = null;
        }, 120);
      } else if (nextAsset.lastPrice && nextAsset.lastPrice.lt(prevAsset.lastPrice)) {
        this.change = 'down';
        this.timerId = setTimeout(() => {
          this.change = null;
        }, 120);
      }
    },
  },

  beforeDestroy() {
    clearTimeout(this.timerId);
    this.timerId = null;
  },

  methods: {
    handleSelect(recommendedContract) {
      if (recommendedContract && recommendedContract.futuresContractHash) {
        this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
        this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
        this.$router.push({ name: 'future', params: { contract: recommendedContract.futuresContractHash } });
      } else {
        this.$store.commit('ui/ADD_NOTIFICATION', {
          status: 'error',
          headline: 'No futures contract hash',
          message: 'No futures contract hash for selected asset',
        });
      }
    },
  },

  components: {
    EBigNumber,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-asset-crypto-item {
  &__increase .the-assets-crypto__item-price {
    background-color: rgba($color-green-bright, 0.25);
    animation-name: eorderbooksizeanimation;
    animation-duration: .05s;
  }

  &__decrease .the-assets-crypto__item-price {
    background-color: rgba($color-red, .25);
    animation-name: eorderbooksizeanimation;
    animation-duration: .05s;
  }

  &__asset-link {
    width: 10px;
    height: 10px;

    svg g {
      fill: $color-green-dark;
    }
  }

  &__asset-symbol {
    display: flex !important;
    justify-content: space-between !important;
    padding: 0 4px 0 6px !important;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
}

@keyframes eorderbooksizeanimation {
  0%   {
    opacity: 0.5;
  }
  75% {
    opacity: 0.5;
  }
}
</style>




