<template>
  <div
    class="the-assets-crypto"
    :class="{
      'the-assets-crypto__light': theme === 'light',
      'the-assets-crypto__dark': theme === 'dark',
      // 'the-assets-crypto__test': APP_ENV === 'test'
     }"
  >
    <div class="the-assets-crypto__headline">
      <div class="the-assets-crypto__item-col">Name</div>
      <div class="the-assets-crypto__item-col">Price</div>
      <div class="the-assets-crypto__item-col">Volume</div>
      <div class="the-assets-crypto__item-col">Change</div>
      <div class="the-assets-crypto__item-col">
        <span class="the-assets-crypto__link-placeholder"><e-icon :src="$options.icons.iconLink" class="the-assets-crypto__link-icon"/></span>
      </div>
      <!--<div class="the-assets-crypto__item-col">Leverage</div>-->
    </div>

    <e-container-scroll class="the-assets-crypto__scroll">

      <div class="the-assets-crypto__body">

        <e-asset-crypto-item
          v-for="asset in assetsToDisplay"
          :activeAsset="activeAsset"
          :key="asset.id"
          :asset="asset"
        />
      </div>
    </e-container-scroll>
    <!--<div class="the-assets-crypto__preloader" v-if="notLoaded">-->
      <!--<e-preloader/>-->
    <!--</div>-->
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters, mapState } from 'vuex';
import get from 'lodash/get';
import findIndex from 'lodash/findIndex';
import throttle from 'lodash/throttle';
import memoize from 'lodash/memoize';

import { getAssets } from '@/api/backend/futures/assets';
import { onTradeChange, removeTradeChangeHandler } from '@/api/backend/futures/trades';
import { ASYNC_STATES, FUTURES_AMOUNT_DECIMALS } from '@/util/constants';

import EContainerScroll from '@/components/EContainerScroll';
import EAssetCryptoItem from '@/components/EAssetCryptoItem';
import EIcon from '@/components/EIcon';
import EPreloader from '@/components/EPreloader';
import EBigNumber from '@/components/EBigNumber';

import iconLink from '@/icons/e-icon__link2.svg';

/**
 * @property requestState
 */
export default {
  name: 'the-assets-crypto',

  props: {
    mobile: {
      type: Boolean,
      default: false,
    },

    type: {
      type: String,
    },
  },

  data() {
    return {
      handleNewTrade: null,
      assetsToDisplay: [],
      requestState: ASYNC_STATES.INITIAL,
      onFuturesContractsHandler: undefined,
      loadFuturesAssetsInterval: undefined,
    };
  },

  icons: {
    iconLink,
  },

  computed: {
    ...mapGetters({
      contractState: 'futuresActiveContract/contractState',
      baseToken: 'balance/baseToken',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
      activeAsset: state => state.futuresActiveContract.activeContract.asset,
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    notLoaded() {
      return [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.requestState);
    },
  },

  watch: {
    baseToken() {
      this.loadFuturesAssets();
    },

    type() {
      this.loadFuturesAssets();
    },
  },

  mounted() {
    this.loadFuturesAssets();

    this.$store.dispatch('tokens/loadTokens')
      .then(() => {
        this.loadFuturesAssetsInterval = setInterval(this.loadFuturesAssets.bind(this), 1000 * 120);
      });

    this.handleNewTrade = (tradeEvt) => {
      const assetName = get(tradeEvt, 'data.futures_asset.name');
      const getAssetIndex = memoize(findIndex.bind(this, this.assetsToDisplay));
      const assetIndex = getAssetIndex(a => a.name === assetName);

      if (this.assetsToDisplay[assetIndex]) {
        this.assetsToDisplay[assetIndex] = { ...this.assetsToDisplay[assetIndex], lastPrice: BigNumber(get(tradeEvt, 'data.price')) };

        const volume = BigNumber(get(tradeEvt, 'data.amount_filled') * get(tradeEvt, 'data.price')).shiftedBy(-FUTURES_AMOUNT_DECIMALS);
        this.assetsToDisplay[assetIndex] = { ...this.assetsToDisplay[assetIndex], volume24h: this.assetsToDisplay[assetIndex].volume24h.plus(volume) };

        this.assetsToDisplay = [...this.assetsToDisplay];
      }
    };

    onTradeChange(throttle(this.handleNewTrade, 500));
  },

  beforeDestroy() {
    clearInterval(this.loadFuturesAssetsInterval);
    removeTradeChangeHandler(this.handleNewTrade);
    this.handleNewTrade = null;
  },

  methods: {
    async loadFuturesAssets() {
      this.requestState = ASYNC_STATES.PENDING;
      const request = getAssets({ contractAddress: this.activeFuturesSmartContract.contractAddress, baseToken: this.baseToken, type: this.type });
      this.actualRequest = request;
      try {
        const assets = await this.actualRequest;
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.SUCCESS;
        this.$store.commit('tokens/SET_ASSETS', assets);

        this.assetsToDisplay = assets.slice(0, 50);
      } catch (err) {
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.ERROR;
      }
    },

    handleSelect(futuresContractHash) {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
      this.$router.push({ name: 'future', params: { contract: futuresContractHash } });
    },
  },

  components: { EIcon, EPreloader, EBigNumber, EContainerScroll, EAssetCryptoItem },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-assets-crypto {
  $this: &;

  position: relative;
  height: 100%;

  @include r(1279) {
    height: auto;
  }

  &__headline {
    text-transform: uppercase;
    font-size: 9px;
    font-weight: 500;
    letter-spacing: 0;
    color: #5C6062;
    background: #FBFBFD;
    border-bottom: 1px solid #F0F6F9;

    #{$this}__item-col {
      font-size: 9px;
      display: flex !important;
      padding: 8px 4px !important;
    }
  }

  &__headline,
  &__body-item {
    display: flex;
  }

  &__item-col {
    display: flex;
    align-items: center;
    padding: 8px 4px;
    box-sizing: border-box;
    border-right: 1px solid #F0F6F9;
    flex: 0 0 20%;
    height: 32px;
    font-size: 10px;

    &:nth-child(1) {
      display: block;
      flex: 0 0 15.5%;
      justify-content: flex-start;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      min-width: 0;
      padding: 12px 4px 0 6px;
    }

    &:nth-child(2) {
      flex: 0 0 21%;
    }

    &:nth-child(3) {
      flex: 0 0 29.5%;
      justify-content: flex-end;
    }

    &:nth-child(4) {
      flex: 0 0 21%;
      justify-content: center;
    }

    &:nth-child(5) {
      display: flex;
      flex: 0 0 13%;
      justify-content: center;
      padding: 0;
    }

    &:last-child {
      border-right: none;
    }
  }

  &.the-assets-crypto__test {
    font-size: 10px;

    #{$this}__item-col {
      flex: 0 0 17%;

      &:nth-child(1) {
        flex: 0 0 17%;
      }

      &:nth-child(2) {
        flex: 0 0 21%;
      }

      &:nth-child(3) {
        flex: 0 0 21.5%;
      }

      &:nth-child(4) {
        flex: 0 0 18%;
      }

      &:nth-child(5) {
        flex: 0 0 22.5%;
      }
    }

    .e-big-number {
      font-size: 10px;
    }
  }

  .e-container-scroll {
    overflow-y: scroll;
  }

  &__body-item {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 11px;
    font-weight: 500;
    color: #071050;
    letter-spacing: 0;
    line-height: 10px;
    transition: color $transition-time, background-color .15s ease-in-out;
    border-bottom: 1px solid #F0F6F9;
    cursor: pointer;

    @include r(1279) {
      border-bottom: none;
    }

    &:hover {
      background: #FBFBFD;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 2px;
        height: 100%;
        background: $color-blue;
      }
    }

    &.is-active {
      background: #f6f8fd;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 2px;
        height: 100%;
        background: #00DB9C;
      }
    }

    .e-big-number {
      font-size: 11px;
      font-weight: 400;

      &__leading-zeros,
      &__trailing-zeros {
        opacity: 1;
      }

      &__int {
        .e-big-number__main + .e-big-number__trailing-zeros {
          opacity: 1;
        }
      }

      @include r(1439) {
        font-size: 10px;
      }
    }
  }

  &__symbol {
    width: 28px;
    height: 13px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 9px;
    font-weight: 600;
    color: #8D9098;
    background: rgba(1, 34, 231, 0.1);
    border-radius: 3px;
  }

  .e-big-number {
    font-size: 11px;
    color: $color-dark-blue;
    font-weight: 400;
  }

  &__change-increase {
    color: #1FCE43;

    .e-big-number {
      color: #1FCE43;
      opacity: 1;
    }
  }

  &__change-decrease {
    color: #DA4830;

    .e-big-number {
      color: #DA4830;
      opacity: 1;
    }
  }

  &__margin {
    font-size: 10px;
    font-weight: 600;
    width: 30px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $color-blue-dark-mode;
    color: $color-blue-dark-mode;
    border-radius: 2px;
  }

  &__scroll {
    height: calc(100% - 65px) !important;
  }

  &__link-placeholder {
    width: 10px;
    height: 10px;

    svg #e-icon-link2-ui-kit {
      fill: #000;
      opacity: .5;
    }
  }

  &__dark {
    color: #fff;

    #{$this}__headline {
      color: #8D9098;
      background: $dark-background-lighter;
      border-bottom: 1px solid #282C38;

      #{$this}__item-col {
        border-color: #282C38;
      }
    }

    #{$this}__item-col,
    #{$this}__body-item {
      border-color: #282C38;
    }

    #{$this}__body-item {
      color: #CACCD3;

      #{$this}__item-col {
        border-color: #282C38;
      }

      &:hover {
        background: #1C222D;
        &::before {
          background: $color-blue-dark-mode;
        }
      }

      &.is-active {
        background: #1d2331;

        #{$this}__item-col:first-child {
          color: #00DB9C;
        }
      }
    }

    .e-big-number {
      color: #fff;
    }

    #{$this}__change-increase {
      color: #00DB9C;

      .e-big-number {
        color: #00DB9C;
      }
    }

    #{$this}__change-decrease {
      color: #DA4830;

      .e-big-number {
        color: #DA4830;
      }
    }

    #{$this}__symbol {
      color: #8D9098;
      background: #23262d;
    }

    #{$this}__link-placeholder {
      svg #e-icon-link2-ui-kit {
        fill: #fff;
      }
    }
  }
}
</style>



